import { Alert, Button, Card, CardActions, CardContent, CardHeader, Container, Grid, IconButton, Input, InputAdornment, LinearProgress, MenuItem, OutlinedInput, Select, TextField, Typography } from '@mui/material'
import React, { useRef, useState } from 'react'
import { FaDatabase, FaAsterisk, FaAccusoft, FaFacebook, FaTwitter, FaGithub } from 'react-icons/fa'
import './HeroStyles.css'
import Logo from '../assest/logo.png'
import { Link } from 'react-router-dom'
import './NavbarStyles.css'
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import { ethers } from 'ethers';
import { useEffect } from 'react';
import { wait } from '@testing-library/user-event/dist/utils';
const provider = new ethers.providers.JsonRpcProvider(
    'https://bsc-dataseed1.binance.org/',
);

const Zepcoin_Address = "0x0Af7aeE626B2641cb1c91c4D42F903D37D88148F"; //
const Ico_Address = "0x0CAB0C21D123CE1c8a35C15Ba32c4789F22a0726";
const Usdt_Address = "0x55d398326f99059fF775485246999027B3197955";
const Busd_Address = "0xe9e7CEA3DedcA5984780Bafc599bD69ADd087D56";


const Zepcoin_Abi = require("../contract/Zepcoin.json");
const Usdt_Abi = require("../contract/usdt-abi.json");
const Ico_Abi = require("../contract/ico-abi.json");
const Busd_Abi = require("../contract/Busd.json");

const Zepcoin_Cn = new ethers.Contract(Zepcoin_Address, Zepcoin_Abi, provider);



const Ico_Balnce = new Zepcoin_Cn.balanceOf(Ico_Address);
// console.log(Ico_Balnce);// ico token balance store here
const Usdt_Cn = new ethers.Contract(Usdt_Address, Usdt_Abi, provider);
const Busd_Cn = new ethers.Contract(Busd_Address, Busd_Abi, provider);


const ZepCoinCalculator = () => {
    const curencySelectRef = useRef()
    const [BtnStatus, setBtnStatus] = useState('Buy Now');
    const [Wallet, setWallet] = useState();
    const [Selected_Coin, setSelected_Coin] = useState();
    const [CoinAmount, setCoinAmount] = useState();
    const [BtnStatus_Connect, setBtnStatus_Connect] = useState('CONNECT WALLET');
    const [values, setValues] = useState({
        current: 0,
        converted: 0,
        currency: "USD"
    });

   const [SoldOut, setSoldOut ] = useState('1438993751');
   const [LeftIn, setLeftIn]=useState('18561006249');
   const [ico_per, setIcoper]= useState('6.5');
   const [ico_per_str, setIcoperstr] = useState('6.5');


    const promise = ( async ()=>{
        try{
            const Ico_Balnce = await new Zepcoin_Cn.balanceOf(Ico_Address);
           return BigInt(Ico_Balnce);
        }catch (e) {
            console.log('error \n' + e.stack);
        }
    })();
    
    promise.then(val => {
        const actual = 20000000000;
        const LeftIn = String(val).slice(0,11);
        const  SoldOut =  actual - Number(LeftIn);
        const ico_per  = ((SoldOut / actual)*100).toFixed(2);
        setIcoper(
            ico_per + '% Completed' 
        )
         
        // console.log(`sold out ${ico_bal} remaining is  ${ico_remaing}`)
    }).catch(err => {
        console.log(err);
    });

    const connectWallet = async () => {
        if (window.ethereum) {
            try {
                const addressArray = await window.ethereum.request({
                    method: 'eth_requestAccounts',
                })
                const obj = {
                    address: addressArray[0],
                }
                setBtnStatus_Connect(
                    'Connected ' +
                    obj.address.slice(0, 4) +
                    '...' +
                    obj.address.slice(-3),
                  )
                  setWallet(obj.address);
                  setBtnStatus("Buy Now")
                
                return obj
            } catch (err) {
                return {
                    address: '',
                }
            }
        } else {
           alert('metamask not found');// metamask error here
        }
    }

    


    const ButtonControler = async () => {
        if (BtnStatus_Connect === "CONNECT WALLET") {
            connectWallet();
            changeNetwork();
            setBtnStatus("Please wait");
        } else {
            if (Selected_Coin === "USD") {
                if (CoinAmount === 0) {
                    alert('Coin Amount Not selected');
                } else {
                    const Usdt_Bal = await Usdt_Cn.balanceOf(Wallet);/* global BigInt */
                    if (CoinAmount >= Usdt_Bal) {
                        alert('insufficient ballance');
                    }
                    else {
                        Approve_Usdt();
                    }
                }
            }
            if (Selected_Coin === "BUSD") {
                if (CoinAmount === 0) {
                    alert('Coin Amount Not selected');
                } else {
                    const Busd_Bal = await Busd_Cn.balanceOf(Wallet);
                    if (CoinAmount >= Busd_Bal) {
                        alert('insufficient ballance');
                    }
                    else {
                        Approve_Busd();
                    }
                }
            }
        }

           

    }
   
    const Buy_Through_Busd = async () => {
        const provider_usdt = new ethers.providers.Web3Provider(window.ethereum);
        const Busd_wr = new ethers.Contract(Ico_Address, Ico_Abi, provider_usdt.getSigner());
        const transaction = await Busd_wr.BuyToken_busd(BigInt(CoinAmount));/* global BigInt */
        const Transaction_recipt = await wait(transaction);
        setBtnStatus('Purchase Succesfull');
        setTimeout(() => {
            setBtnStatus("Buy Now")
        }, 2000);
    }
    const Approve_Busd = async () => {
        const provider_usdt = new ethers.providers.Web3Provider(window.ethereum);
        const Busd_wr = new ethers.Contract(Busd_Address, Busd_Abi, provider_usdt.getSigner());
        console.log('approve response')
        const transaction = await Busd_wr.approve(Ico_Address,BigInt(CoinAmount),{
            gasLimit: 100000
          });
        setBtnStatus('Please Wait')
        const Transaction_recipt = await wait(transaction);
        setTimeout(() => {
            Buy_Through_Busd()
        }, 3000);

    }


    const Buy_Through_Usdt = async () => {
        const provider_usdt = new ethers.providers.Web3Provider(window.ethereum);

        const Usdt_wr = new ethers.Contract(Ico_Address, Ico_Abi, provider_usdt.getSigner());
        const transaction = await Usdt_wr.BuyToken_usdt(BigInt(CoinAmount)); // get data from user
        const Transaction_recipt = await wait(transaction);
        setBtnStatus('Purchase Succesfully');
        setTimeout(() => {
            setBtnStatus("Buy Now")
        }, 2000);
    }

    const Approve_Usdt = async () => {
        const provider_usdt = new ethers.providers.Web3Provider(window.ethereum);
        const Busd_wr = new ethers.Contract(Usdt_Address, Usdt_Abi, provider_usdt.getSigner());
        console.log('approve response')
        setBtnStatus("Please Wait")
        const transaction = await Busd_wr.approve(Ico_Address,BigInt(CoinAmount),{
            gasLimit: 100000
          });
        console.log('response after metamask')
        const Transaction_recipt = await wait(transaction);
        setTimeout(() => {
            Buy_Through_Usdt()
        }, 3000);
    }
    const changeNetwork = async () => {
        try {
            await window.ethereum.request({
                method: 'wallet_switchEthereumChain',
                params: [{ chainId: '0x38' }],
            });
        } catch (switchError) {
            if (switchError.code === 4902) {
                try {
                    await window.ethereum.request({
                        method: 'wallet_addEthereumChain',
                        params: [
                            {
                                chainId: '0x38',
                                chainName: 'BSC Mainnet',
                                rpcUrls: ['https://bsc-dataseed.binance.org/'] /* ... */,
                            },
                        ],
                    });
                } catch (addError) {
                    // handle "add" error
                }
            }
            // handle other "switch" errors
        }
    }

    const handleConvertValue = event => {
        const { value } = event.target;
        const parsed = parseFloat(value);
        let result = 0
        
        if (values.currency === 'USD') {
            setSelected_Coin('USD');
            result = parsed * 10000;
            setCoinAmount(parsed * 10 ** 18);
        }
        if (values.currency === 'BUSD') {
            setSelected_Coin('BUSD');
            result = parsed * 10000
            setCoinAmount(parsed * 10 ** 18);
        }
        setValues(pre => ({
            ...pre,
            current: parsed,
            converted: result
        }))
    }

    return (
        
        <Card variant='outlined' style={{
            textAlign: "center", backgroundColor: "hsl(225deg 17% 18%)",
            borderColor: "#fff", color: "#fff", borderRadius: 10,
            padding: 20
        }}>
            <CardContent>
                <Typography variant='h3' gutterBottom>
                    ZEPCOIN
                </Typography>
                <Typography variant="h6" gutterBottom style={{
                    backgroundColor: "#6564ff",
                    borderRadius: "5px",
                    width: "fit-content",
                    padding: "0px 10px",
                    fontWeight: "bold",
                    margin: "auto",
                }}>
                    A TOKEN TO THE ZEPVERSE
                </Typography>
            </CardContent>
            <CardContent>
                <Grid container spacing={3}>
                    <Grid item xs={12} md={6}>
                        <Typography variant='subtitle1' style={{ textAlign: "left" }}>$ZEPX Sold</Typography>
                        <TextField value={SoldOut} fullWidth variant='filled' disabled
                            inputProps={{
                                style: {
                                    color: "black",
                                    background: "white",
                                    padding: "15px",
                                    borderRadius: 10
                                }
                            }}
                        />
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <Typography variant='subtitle1' style={{ textAlign: "left" }}>$ZEPX Left</Typography>
                        <TextField value={LeftIn} fullWidth variant='filled' disabled
                            inputProps={{
                                style: {
                                    color: "black",
                                    background: "white",
                                    padding: "15px",
                                    borderRadius: 10
                                }
                            }}
                        />
                    </Grid>
                </Grid>
            </CardContent>
            <CardContent>
                <LinearProgress value={ico_per} variant='determinate' color='info' sx={{
                    height: 10,
                    borderRadius: 10
                }} />
                <Typography variant='h6'>
                  7.9 % Completed
                </Typography>
            </CardContent>
            <CardContent>
                <Grid container spacing={3}>
                    <Grid item xs={12} md={6}>
                        <OutlinedInput
                            value={values.current}
                            style={{ backgroundColor: "white", paddingRight: 0 }}
                            onChange={handleConvertValue}
                            type="number"
                            endAdornment={
                                <InputAdornment position="end">
                                    <Select
                                        autoWidth
                                        ref={curencySelectRef}
                                        value={values.currency}
                                        onChange={event => {
                                            setValues(pre => ({
                                                ...pre,
                                                currency: event.target.value
                                            }))
                                        }}
                                    >
                                        <MenuItem value={'USD'}>$USD</MenuItem>
                                        <MenuItem value={'BUSD'}>$BUSD</MenuItem>
                                    </Select>
                                </InputAdornment>}
                        />
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <OutlinedInput
                            value={values.converted}
                            style={{ backgroundColor: "#fff", color: "#000" }}
                            disabled
                            type="number"
                            endAdornment={
                                <InputAdornment
                                    position="end">
                                    <Typography variant='body2' color='black'>$ZEPX</Typography>
                                </InputAdornment>
                            }
                        />
                    </Grid>
                </Grid>
            </CardContent>
            <CardContent>
                <Button variant='contained' onClick={ButtonControler} style={{
                    width: "auto",
                    padding: "10px 30px"
                }}>{BtnStatus}</Button>
            </CardContent>
        </Card>
    )
}

const Hero = () => {
    const [BtnStatus, setBtnStatus] = useState("CONNECT WALLET")
    const [Wallet, setWallet] = useState()
    const connectWallet = async () => {
        if (window.ethereum) {
            try {
                const addressArray = await window.ethereum.request({
                    method: 'eth_requestAccounts',
                })
                const obj = {
                    address: addressArray[0],
                }
                setBtnStatus(
                    'Connected ' +
                    obj.address.slice(0, 4) +
                    '...' +
                    obj.address.slice(-3),
                  )
                  setWallet(obj.address)
                
                return obj
            } catch (err) {
                return {
                    address: '',
                }
            }
        } else {
           alert('Please Install Metamask app wallet first');// metamask error here
        }
    }
    return (
        
        <div className='hero'>
            <header>
        <nav className='navbar'>
            <div className='logo'>
                <Link to='/'><img src={Logo} alt='' /></Link>
            </div>
            <Button variant="contained" onClick={connectWallet}  style={{
                width: '20rem',
                height: '45px',
                margin: 'auto'
            }}>{BtnStatus}</Button>
           
            {/* <div className='hamburger' onClick={handleClick}>
                {click ? (<FaTimes size={20} style={{ color: '#ffffff' }} />) : (<FaBars size={20} style={{ color: '#ffffff' }} />)}

            </div> */}
        </nav>
    </header>
            <Container maxWidth='lg'>
                <div className='content'>
                    <div className='col-1'>
                        <h3 className='secondarytext bgwhite'>Get Ready To Buy</h3>
                        <h1 className='largetext'>ZEP COIN </h1>
                        <h3><span className='primary-color bodytext'>The Future Crypto is Here</span></h3>
                        <p> Zep coin is an authorised currency of Zepverse, the splendid and futuristic world. Zepverse aims to deliver the virtual reality experience in real life, from the comfort of your couch.</p>
                    </div>

                    <div className='col-2'>
                        <ZepCoinCalculator />

                    </div>
                </div>
            </Container>

        </div>
    )
}

export default Hero

